.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .header h1 {
    font-size: 24px;
    font-weight: bold;
  }

  .settings-button {
    padding: 5px 10px;
    background: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
  }

  .dialog {
    margin-bottom: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }

  .dialog h2 {
    margin-bottom: 10px;
  }

  .api-input {
    width: 100%;
    padding: 5px;
    margin-bottom: 10px;
  }

  .save-button {
    padding: 5px 10px;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  .form-group {
    display: flex;
    flex-direction: column;
  }

  .form-group label {
    margin-bottom: 5px;
  }

  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 5px;
  }

  .form-group textarea {
    min-height: 100px;
  }

  .submit-button {
    padding: 10px;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .submit-button:disabled {
    background: #cccccc;
    cursor: not-allowed;
  }

  .error-message {
    margin-top: 20px;
    padding: 10px;
    background: #FFCCCB;
    border-radius: 4px;
  }

  .error-message p {
    color: #D8000C;
  }

  .generated-ad {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }

  .generated-ad h2 {
    margin-bottom: 10px;
  }

  .generated-ad h3 {
    font-weight: bold;
    margin-top: 10px;
  }